body {
  color: var(--main-text-color); /*--основной цвет текста--*/
  background-color: var(--main-background-color);
  font-family: 'Roboto', sans-serif;
  }

/*--Сброс дефолтных настроек--*/

/*--Сброс настроек списков списков ul, ol--*/
.list
{list-style: none;}

ul, ol
{margin-top: 0;
margin-bottom: 0;
padding-left: 0;   
}    

/*--Сброс настроек ссылок a--*/
.link
{text-decoration: none;}

/*--Сброс настроек заголовков h и текста p--*/
h1, h2, h3, p
{margin-top: 0;
margin-bottom: 0;}

/*--Сбрасываем нижний отступ в изображениях, делая их блоковыми--*/
img 
{display: block;
max-width: 100%;
height: auto;}

/*--Задание палитры цветов через переменные--*/
:root
{
--main-text-color: #434455;
--main-background-color:#FFFFFF;
--logo-check-color:yellow;
--header-word-color:blue;
--nav-link-color:#2E2F42;
--link-active-color:#404BBF;
--hero-background-color:#2E2F42;
--hero-title-color:#FFFFFF;
--main-btn-color:#FFFFFF;
--btn-color: #4D5AE5;
--border-btn-color: #e7e9fc;
--section-main-text-color: #2E2F42;
--section-team-background-color:#F4F4FD;
--footer-background-color:#2E2F42;
--footer-main-text-color:#F4F4FD;
--footer-secondary-text-color: #fff;
--icon-fill-color: #f4f4fd;
}


/*----------------------HEADER-----------------------*/

/*-----------------МОБИЛЬНАЯ ВЕРСИЯ-------------------*/


/*--Описываем подчеркивание header--*/
.header {
position: fixed;
top: 0;
left: 0;
width: 100%;
z-index: 999; 
/* background-color: #333;
color: #fff; */
padding: 10px;
text-align: center;
background-color: rgba(255, 255, 255, 1);
border-bottom: 1px solid #E7E9FC;
box-shadow: 0px 2px 1px rgba(46, 47, 66, 0.08),
  0px 1px 1px rgba(46, 47, 66, 0.16), 0px 1px 6px rgba(46, 47, 66, 0.08);
}


/*--Контейнер, в который помещается контент секций--*/
.container {
max-width: 428px;
padding-left: 16px;
padding-right: 16px;
margin-left: auto;
margin-right: auto;
}


/*--Скрываем ненужные меню для мобайла--*/
.nav-ul, .cont 
{ display: none; }  


/*--Данные верхнего логотипа--*/
.logo {
  color: var(--logo-check-color);
  font-family: 'Raleway', sans-serif;
  font-weight: 800;
  font-size: 18px;
  line-height: 1.17;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}

/*--Дополнительные данные верхнего логотипа--*/
.head {
padding: 24px 0;
}

/*--Задаем отступ между логотипом и nav-ul--*/

/*--Описываем слово Studio--*/
.header-word {
  color: var(--header-word-color);
  font-family: 'Raleway' sans-serif;
  font-weight: 800;
  font-size: 18px;
  line-height: 1.17;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}

/*--Ставим элементы навигации в строку--*/
.main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  }


/*--Кнопка открытия мобильного меню - бургер--*/
.mobile-menu-open-btn
{ border: none;
background-color: transparent;
padding: 0;
line-height: 0;
padding-top: 24px;
padding-bottom: 24px;
}

/*--Мобильное меню--*/
.mobile-menu
{
position: fixed;
width: 100%;
height: 100%;
z-index: 1000;
top: 0;
right: 0;
width: 200px;
height: 520px;
opacity: 0;
visibility: hidden;
pointer-events: none;
background-color: #696c92;
transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
          visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

transform: translateX(100%);

/* border-radius: 15px; */
}


/*--Открытие мобильного меню--*/
.mobile-menu.is-open
{ opacity: 0.95;
visibility: visible;
pointer-events: auto; 

transform: translateX(0);
}

/*--Контейнер мобильного меню--*/
.mobile-menu-container
{
display: flex;
height: 75%;
flex-direction: column;
justify-content: space-between;
position: relative;
padding-top: 80px;
padding-left: 25px;
padding-bottom: 40px;
padding-right: 25px;
/* gap: 40px; */
}


/*--Кнопка закрытия мобилього меню--*/
.mobile-menu-close-btn
{position: absolute;
top: 24px;
right: 23px;
display: flex;
align-items: center;
justify-content: center; 
width: 24px; 
height: 24px; 
border: 1px solid #D9D9D9;
border-radius:50%;
background-color: #ffff; 
padding: 0;
}


/*--Описываем иконку закрытия мобильного меню--*/
.icon-mobile-menu-close 
{ 
stroke: #696c92;
/* background-color: #696c92; */
width: 16px;
height: 16px;
}


/*--Мобильное меню навигации--*/
.mobile-menu-nav
{display: flex;
flex-direction: column;  
padding: 0;
gap: 40px;
}


/*--Описываем линки навигации мобильного меню--*/
.mobile-menu-link
{font-weight: 500;
font-size: 16px;
line-height: 1.11;
letter-spacing: 0.02em;
text-decoration: none;
}


/*--Задаем цвет главной ссылке мобильного меню--*/
.main {
 /* color: #404bbf; */
 color: #FFFFFF;  
}


/*--Задаем цвет другим ссылкам мобильного меню--*/
.next
{ color: #FFFFFF;  
}


/*--Описываем поведение ссылок при фокусе--*/
.mobile-menu-link:focus
{ color: #404bbf; }


/*----*/ 
.mobile-cont-sl
{ display: flex;
flex-direction: column; }


/*--Мобильное меню контакты--*/
.mobile-menu-cont
{display: flex;
flex-direction: column;   
gap: 40px; }


/*--Задаем цвет ссылке телефона--*/
.tel-number
{ color: #4D5AE5; }


/*--Параметры текста email--*/
.mobile-menu-email
{font-weight: 500;
font-size: 20px;
line-height: 1.2;
letter-spacing: 0.02em;
color: #434455;
text-decoration: none;
}


/*--Список соцсетей мобильного меню--*/
.mobile-menu-social
{display: flex;
margin-top: 48px;
max-width: 328px;
justify-content: space-between;
}


/*--Ячейка каждой иконки линка соцсети--*/
.social-link
{width: 40px;
height: 40px;}


/*--Описание класса ссылки соцсетей мобильного меню--*/
.social-link-a-mobile {
   display: flex;
   width: 100%;
   height: 100%;
   background-color: #4D5AE5;
   border-radius: 50%;
   align-items: center;
   justify-content: center;
   color: #FFFFFF;
   transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }


/*--Меняем цвет при наведении на ссылку соцсети--*/
.social-link-a-mobile:hover,
.social-link-a-mobile:focus {
   background-color: #404bbf;
  }

/*--Класс для заливки иконок соцсетей футера--*/
.icon-sl-mobile 
{ fill: #f4f4fd; }


/*---Кнопка Donate---*/
.main-btn-donate {
color: var(--main-btn-color);
background-color: #4D5AE5;
font-family: 'Roboto', sans-serif;
font-weight: 500;
font-size: 16px;
line-height: 1.5;
letter-spacing: 0.04em;
border: none;
display: block;
cursor: pointer;
width: 1px;
/* min-width: 169px; */
height: 56px;
margin-left: auto;
/* margin-right: auto; */
/* margin-top: 72px; */
padding: 16px 20px 16px 20px;
border-radius: 4px;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
visibility: hidden; 
}

/*---Кнопка Donate---*/
.main-btn-donate-mobile {
color: var(--main-btn-color);
background-color: #4D5AE5;
font-family: 'Roboto', sans-serif;
font-weight: 500;
font-size: 15px;
line-height: 1.5;
letter-spacing: 0.04em;
border: none;
display: block;
cursor: pointer;
max-width: 169px;

/* min-width: 169px; */
height: 56px;
/* margin-left: auto; */
/* margin-right: auto; */
 /* margin-top: 80px; */
padding: 16px 20px 16px 20px;
border-radius: 4px;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
/* visibility: hidden;  */
}


