/*--------------------BACK-DROP-----------------------*/

/*----------МОБИЛЬНАЯ ВЕРСИЯ----------*/

.backdrop
{
position: fixed;
z-index: 100;
top: 0;
left: 0;
width: 100%;
height: 100%;
background-color: rgba(46, 47, 66, 0.4);
transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

/*--Дополнительный класс бекдропа--*/
.backdrop.is-hidden
{visibility: hidden;
opacity: 0;
pointer-events: none;
}


/*--Меняем размеры модального окна при вьюпорте 
  меньше 428рх для нормального отображения--*/
@media screen and (max-width: 427px) {
.modal-window {
  width: 310px;
  height: 615px;
}
}


/*--Меняем размеры модального окна при вьюпорте 
  больше 428рх для нормального отображения--*/
@media screen and (min-width: 428px) {
.modal-window {
  width: 392px;
  height: 590px;}}


/*--Описание модального окна--*/
.modal-window
{
background-color: #FCFCFC;
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12),
      0px 2px 1px rgba(0, 0, 0, 0.2);
border-radius: 4px;       
transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
padding-left: 16px;
padding-right: 16px;
padding-top: 72px;
padding-bottom:24px;
}


/*--Описание кнопки закрытия модального окна--*/
.modal-close-btn
{
position: absolute;
top: 24px;
right: 24px;
display: flex;
align-items: center;
justify-content: center;    
background-color: #E7E9FC;
padding: 0;
width: 24px;
height: 24px;
border-radius: 50%;
border: 1px solid rgba(0, 0, 0, 0.1);
cursor: pointer;
transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
          border 250ms cubic-bezier(0.4, 0, 0.2, 1);
}


/*--Поведение кнопки закрытия модального окна при ховере, фокусе--*/
.modal-close-btn:hover,
.modal-close-btn:focus
{background-color: #404BBF;
border: none;
}


/*-Описание иконки закрытия модального окна-*/
.modal-close-btn-icon
{fill: #2E2F42;
transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);}


/*--Меняем цвет иконки при ховере/фокусе модальной кнопки закрытия--*/
.modal-close-btn:hover .modal-close-btn-icon,
.modal-close-btn:focus .modal-close-btn-icon
{fill: #fff;}


/*--Надпись над формами--*/
