body {
  color: var(--main-text-color);
  background-color: var(--main-background-color);
  font-family: Roboto, sans-serif;
}

.list {
  list-style: none;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.link {
  text-decoration: none;
}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --main-text-color: #434455;
  --main-background-color: #fff;
  --logo-check-color: yellow;
  --header-word-color: blue;
  --nav-link-color: #2e2f42;
  --link-active-color: #404bbf;
  --hero-background-color: #2e2f42;
  --hero-title-color: #fff;
  --main-btn-color: #fff;
  --btn-color: #4d5ae5;
  --border-btn-color: #e7e9fc;
  --section-main-text-color: #2e2f42;
  --section-team-background-color: #f4f4fd;
  --footer-background-color: #2e2f42;
  --footer-main-text-color: #f4f4fd;
  --footer-secondary-text-color: #fff;
  --icon-fill-color: #f4f4fd;
}

.header {
  width: 100%;
  z-index: 999;
  text-align: center;
  background-color: #fff;
  border-bottom: 1px solid #e7e9fc;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.container {
  max-width: 428px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}

.nav-ul, .cont {
  display: none;
}

.logo {
  color: var(--logo-check-color);
  letter-spacing: .03em;
  text-transform: uppercase;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 1.17;
}

.head {
  padding: 24px 0;
}

.header-word {
  color: var(--header-word-color);
  font-family: "Raleway" sans-serif;
  letter-spacing: .03em;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 800;
  line-height: 1.17;
}

.main-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.mobile-menu-open-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 24px 0;
  line-height: 0;
}

.mobile-menu {
  width: 100%;
  height: 100%;
  z-index: 1000;
  width: 200px;
  height: 520px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: #696c92;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  opacity: .95;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0);
}

.mobile-menu-container {
  height: 75%;
  flex-direction: column;
  justify-content: space-between;
  padding: 80px 25px 40px;
  display: flex;
  position: relative;
}

.mobile-menu-close-btn {
  width: 24px;
  height: 24px;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 24px;
  right: 23px;
}

.icon-mobile-menu-close {
  stroke: #696c92;
  width: 16px;
  height: 16px;
}

.mobile-menu-nav {
  flex-direction: column;
  gap: 40px;
  padding: 0;
  display: flex;
}

.mobile-menu-link {
  letter-spacing: .02em;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.11;
  text-decoration: none;
}

.main, .next {
  color: #fff;
}

.mobile-menu-link:focus {
  color: #404bbf;
}

.mobile-cont-sl {
  flex-direction: column;
  display: flex;
}

.mobile-menu-cont {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.tel-number {
  color: #4d5ae5;
}

.mobile-menu-email {
  letter-spacing: .02em;
  color: #434455;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
  text-decoration: none;
}

.mobile-menu-social {
  max-width: 328px;
  justify-content: space-between;
  margin-top: 48px;
  display: flex;
}

.social-link {
  width: 40px;
  height: 40px;
}

.social-link-a-mobile {
  width: 100%;
  height: 100%;
  color: #fff;
  background-color: #4d5ae5;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.social-link-a-mobile:hover, .social-link-a-mobile:focus {
  background-color: #404bbf;
}

.icon-sl-mobile {
  fill: #f4f4fd;
}

.main-btn-donate {
  color: var(--main-btn-color);
  letter-spacing: .04em;
  cursor: pointer;
  width: 1px;
  height: 56px;
  visibility: hidden;
  background-color: #4d5ae5;
  border: none;
  border-radius: 4px;
  margin-left: auto;
  padding: 16px 20px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.main-btn-donate-mobile {
  color: var(--main-btn-color);
  letter-spacing: .04em;
  cursor: pointer;
  max-width: 169px;
  height: 56px;
  background-color: #4d5ae5;
  border: none;
  border-radius: 4px;
  padding: 16px 20px;
  font-family: Roboto, sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.5;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }

  .mobile-menu-open-btn {
    display: none;
  }

  .head {
    margin-right: 60px;
  }

  .nav {
    align-items: center;
    display: flex;
  }

  .main-nav {
    display: flex;
  }

  .nav-ul {
    flex-flow: wrap;
    align-items: center;
    gap: 10px 24px;
    display: flex;
  }

  .cont {
    margin-left: auto;
    font-style: normal;
    display: flex;
  }

  .cont-ul {
    flex-direction: column;
    gap: 12px;
    display: flex;
  }

  .nav-link {
    color: var(--nav-link-color);
    letter-spacing: .02em;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .nav-link:hover, .nav-link:focus {
    color: var(--link-active-color);
  }

  .current-page-main {
    color: var(--link-active-color);
    position: relative;
  }

  .cont-link {
    color: var(--main-text-color);
    letter-spacing: .04em;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.17;
    text-decoration: none;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .email {
    padding-top: 16px;
  }

  .tel {
    padding-bottom: 16px;
  }

  .cont-link:hover, .cont-link:focus {
    color: var(--link-active-color);
  }

  .main-btn-donate {
    visibility: visible;
    min-width: 169px;
  }
}

@media screen and (min-width: 1158px) {
  .container {
    max-width: 1158px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .head {
    margin-right: 100px;
  }

  .nav-ul {
    align-items: center;
    gap: 40px;
    display: flex;
  }

  .cont {
    margin-left: auto;
  }

  .cont-ul {
    flex-direction: row;
    gap: 40px;
  }

  .tel {
    padding-bottom: 0;
  }

  .email {
    padding-top: 0;
  }

  .cont-link {
    letter-spacing: .02em;
    padding: 24px 0;
    font-size: 16px;
    line-height: 1.5;
    display: block;
  }

  .nav-link {
    padding-top: 24px;
    padding-bottom: 24px;
    font-size: 18px;
  }
}

.hero {
  max-width: 428px;
  border-bottom: 2px solid #ff0;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 120px 0 60px;
  position: relative;
}

.hero:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #00f;
  position: absolute;
  bottom: -5px;
}

.hero-title {
  color: var(--hero-title-color);
  text-align: center;
  letter-spacing: .02em;
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11;
}

.main-btn {
  color: var(--main-btn-color);
  letter-spacing: .04em;
  cursor: pointer;
  min-width: 169px;
  height: 56px;
  background-color: #4d5ae5;
  border: none;
  border-radius: 4px;
  margin-top: 72px;
  margin-left: auto;
  margin-right: auto;
  padding: 16px 32px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.main-btn:hover, .main-btn:focus {
  color: var(--main-btn-color);
  cursor: pointer;
  background-color: #404bbf;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

@media screen and (min-width: 768px) {
  .hero {
    max-width: 768px;
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 140px 0 70px;
  }

  .hero-content {
    display: flex;
  }

  .hero-title {
    color: var(--hero-title-color);
    text-align: center;
    letter-spacing: .02em;
    max-width: 496px;
    margin-left: auto;
    margin-right: auto;
    font-size: 56px;
    font-weight: 700;
    line-height: 1.07;
  }

  .main-btn {
    margin-top: 36px;
  }
}

@media screen and (min-width: 1158px) {
  .hero {
    max-width: 1440px;
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 160px 0 80px;
  }

  .main-btn {
    margin-top: 48px;
  }
}

.section-team {
