.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

@media screen and (max-width: 427px) {
  .modal-window {
    width: 310px;
    height: 615px;
  }
}

@media screen and (min-width: 428px) {
  .modal-window {
    width: 392px;
    height: 590px;
  }
}

.modal-window {
  background-color: #fcfcfc;
  border-radius: 4px;
  padding: 72px 16px 24px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
